@import '../css/globalStyles.css';

.main-container {
    background-color: var(--background);
}

.home-main-content {
   box-sizing: border-box;
   height: 100vh;
   align-items: center;
   scroll-behavior: smooth;
}

.home-main-content-inner {
    padding-top: 80px;
    background-color: transparent;
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
    box-sizing: border-box;
    scroll-behavior: smooth;
    background-color: var(--background);
}

.home-main-content-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    padding-left: 10%;
    padding-top: 20px;
    height: 100%;
}

.home-main-content-logo-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 20px;
}
.dsc-only-logo {
    width: 100px;
}
.dsc-only-text {
    width: 300px;
}

.home-main-content-text .h2 {
    color: var(--darkText);
    text-align: center;
    margin-top: 10vh;
}

.home-main-content-text .t1 {
    /* font-family: 'Wavehaus-Light'; */
    font-size: 18px;
    color: var(--darkGrey);
    letter-spacing: 0.8px;
    text-align: center;
}

.change-word {
    overflow: hidden;
    position: relative;
    float: right;
    height: 32px;
    padding-top: 5px;
    margin-top: -5px;
    background-color: transparent;
    margin-left: 5px;
    border-radius: 7px;
    width: fit-content;
    background-color: var(--ultraLightGrey);
    transition: 0.55s ease;
    box-shadow: 0px 3px 5px var(--lightGrey);
}

.flip {
    animation: flip 6s cubic-bezier(0.23, 1, 0.32, 1.2) infinite;
    animation-delay: 200ms;
    list-style-type: none;
    padding-left: 0;
}

.change-word ul li {
    /* font-family: 'Wavehaus-Light'; */
    padding: 0 10px;
    height: 45px;
    margin-bottom: 45px;
    display: block;
    color: var(--background);
    font-weight: 600;
}

.home-main-content-image {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-main-content-image img {
    width: 90%;
    margin-bottom: 10%;
}

.home-main-content-down-arrow {
    position: absolute;
    width: 10%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    bottom: 10px;
    align-self: center;
    margin-left: 45%;
    cursor: pointer;
}

.home-main-content-down-arrow-arrow {
    padding: 8px;
    background-color: transparent;
    border-radius: 35px;
    /* box-shadow: 2px 2px 15px #0000002a; */
}


.dsc-def-container {
    width: 100%;
    height: 300px;
    background-color: var(--googleYellow);
    padding: 0 10%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.dsc-def-container .h5 {
    width: 30%;
    color: #202020;
    line-height: 25px;
}

.dsc-def-container img {
    width: 30%;
    margin-left: 10%;
}

.dsc-def-container .t2 {
    width: 60%;
}

.fifty {
    width: 50% !important;
}

.about-us-container {
    background-color: var(--background); 
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;
}

.grid-opportunities {
    width: 100%;
    margin: 0 10%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 10vw;
    row-gap: 40px;
    padding: 50px 10%;
}

.grid-opportunities-item:nth-child(even) {
    flex-direction: row-reverse;
}

.grid-opportunities-item {
    width: 100%;
    height: 70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    text-align: center;
}

.opportunities-icon {
    width: 21px;
    height: 21px;
    margin: 0 25px;   
}

.opportunities-icon svg {
    color: linear-gradient(red, blue);
}

@-webkit-keyframes shine {
    from {
      background-position: 200% center;
    }
  }
  
@keyframes shine {
    from {
      background-position: 200% center;
    }
}
