    right: 0;
}

.move-up {
    transform: translateY(-40px);
    transition: 0.3s ease-out;
}

.move-up-2 {
    transform: translateY(-80px);
    transition: 0.3s ease-out;
}

.move-down {
    transform: translateY(40px);
