.reviews-container {
    margin: 100px 15%;
    padding: 0 0%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 400px;
    justify-content: center;
    box-sizing: border-box;
    width: 70%;
}

.review {
    position: absolute;
}

.r1 {
    top: 30px;
    left: 40px;
}

.r2 {
    top: -30px;
    right: 20px;
}

.r3 {
    bottom: 0;
    left: 30px;
}

.r4 {
    bottom: -30px;
    right: 50px;
}

.bluebg {
    background-color: var(--googleBlue);
}

.mouse {
	width: 25px;
	height: 50px;
	border: 2.5px solid var(--grey);
	border-radius: 60px;
	position: relative;
}

.mouse::before {
    content: '';
    width: 6px;
    height: 10px;
    position: absolute;
    top: 5px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #333;
    border-radius: 6px;
    opacity: 1;
    animation: wheel 2s infinite;
    -webkit-animation: wheel 2s infinite;
}

.video-container {
    width: 80%;
    margin: 20px 20%;
    height: 70vh;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}

.video-container video {
    height: 100%; 
    margin: 0 auto;
    will-change: transform;
    transform: perspective(600px) rotateX(45deg) scale(0.5);
    transition: 0.4s ease-out;
    border-radius: 5px;
    background-color: #1C67E1;
    object-fit: cover;
}

.animate-vid video {
    transform: perspective(600px) rotateX(0deg) scale(1.0) !important;
    transition: 0.4s ease-out;
}

@keyframes wheel {
	to {
		opacity: 0;
		top: 30px;
	}
}


