        height: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding:0 10px;
        margin-top: 20px;
    }
    .feature-item-right .h0 {
        width: 20%;
        display: none;
        float: left;
    }
    .feature-item-right .h2,.feature-item-right .t2 {
        width: 100%;
        text-align: center;
    }
    .feature-item-right .h2 {
        font-size: 28px;
    }
    .feature-item:nth-child(even) {
        flex-direction: column;
    }
    .feature-item:nth-child(even) .feature-item-left {
        padding-right: 0;
    }
    .feature-1 {
        height:100%;
    }
    .join-dsc-card {
        width: 100%;
    }

    .infographic-container {
        grid-template-columns: repeat(4, 1fr)
    }
    .infographic-container .i1 {
        width: 70%;
        top: 320px;
        font-size: 26px;
    }
    infograph .h5 {
        font-size: 14px;
        font-weight: lighter;
    }
    .faq-card .t1 {
        margin-bottom: 30px;
    }
    .reviews-container {
        display: none;
    }
    .main-container {
        width: 100%;
    }
    .spring {
        margin: unset;
        width: unset;
    }
    .join-line {
        margin-bottom: 50px;
    }
}
